import { fetchUrl, getToken, redirectTo, saveToken } from "../util";
import { genderArr } from "../util/constants";
import {
  XANO_USER_SIGN_IN_REDIRECT,
  // XANO_USER_FULL_PATH_PREFERENCES_REDIRECT,
  XANO_USER_DASHBOARD_REDIRECT,
  XANO_USER_PREFERENCES_URL,
  // GOOGLE_OAUTH_USER_CONTINUE_URL,
} from "../util/xano_urls";

let USER_ID;

// ? Google oauth stuff
var formHeaders = []; // ? for google (not really needed but just keep it)
var formResponse = [];
function continueOauth(code) {
  var fetchURL = new URL("https://x8ki-letl-twmt.n7.xano.io/api:ocRzyo-e/oauth/google/continue");
  fetchURL.searchParams.set("REDIRECT_URL", "https://nammax.webflow.io/sign-up-2");
  fetchURL.searchParams.set("code", code);
  fetchURL = fetchURL.toString();
  var newUrl = new URL(document.location.href);
  newUrl.searchParams.delete("code");
  newUrl.searchParams.delete("scope");
  newUrl.searchParams.delete("authuser");
  newUrl.searchParams.delete("hd");
  newUrl.searchParams.delete("prompt");
  history.replaceState(null, "", newUrl.toString());

  fetch(fetchURL, {
    headers: formHeaders,
    method: "GET",
  })
    .then((res) => res.json())
    .then((data) => (formResponse = data))
    .then(() => saveToken(formResponse.token))
    .catch((error) => {
      console.error("Error:", error);
    });
}

window.onload = function () {
  var curUrl = new URL(document.location.href);
  var reason = curUrl.searchParams.get("reason");
  var code = curUrl.searchParams.get("code");

  if (reason) {
    let wb = document.getElementById("warning_block");
    wb.style.display = "block";
    wb.innerText = "Please fill these required details in order to use the app";
  }

  // ? for google from signin/signup page redirect
  if (code) {
    continueOauth(code);
  } else {
   const token = getToken();

    if (token) {
      getUserData(token);
    } else {
      // ? if there is no token
      redirectTo(`${XANO_USER_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  }
};

async function getUserData(token) {
  let data = await fetchUrl(XANO_USER_PREFERENCES_URL, "GET", false, token);

  if (data.isFetchOk !== undefined && !data.isFetchOk) {
    console.error(data.message);
    // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
    return;
  }

  const { user, gyms } = data;
  USER_ID = user.id;

  if (user.weight) document.getElementById("weight").value = user.weight;
  if (user.height) document.getElementById("height").value = user.height;
  if (user.date_of_birth) document.getElementById("date_of_birth").value = user.date_of_birth;

  const gymSelect = document.getElementById("gym_select");
  gyms.forEach((gym) => {
    var opt = document.createElement("option");
    opt.value = gym.id;
    opt.text = gym.location;
    if (gym.id === user.gym_id) opt.selected = true;
    gymSelect.appendChild(opt);
  });

  const genderSelect = document.getElementById("gender_select");
  genderArr.forEach((gender) => {
    var opt = document.createElement("option");
    opt.value = gender;
    opt.text = gender;
    if (gender === data.gender) opt.selected = true;
    genderSelect.appendChild(opt);
  });
}

var Webflow = Webflow || [];
Webflow.push(function () {
  // unbind webflow form handling (keep this if you only want to affect specific forms)
  $(document).off("submit");
  /* Any form on the page */
  $("form").submit(async function (e) {
    e.preventDefault();

    const $form = $(this); // The submitted form

   const token = getToken();
    if (token) {
      $("next-btn").attr("disabled", true);

      const xano_input = {
        date_of_birth: document.getElementById("date_of_birth").value,
        gender: document.getElementById("gender_select").value,
        height: document.getElementById("height").value,
        weight: document.getElementById("weight").value,
        gym_id: parseInt(document.getElementById("gym_select").value),
      };

      let preferenceData = await fetchUrl(`${XANO_USER_PREFERENCES_URL}/${USER_ID}`, "POST", xano_input, token);

      if (preferenceData.isFetchOk !== undefined && !preferenceData.isFetchOk) {
        console.error(preferenceData.message);
        $form
          .siblings(".w-form-done")
          .hide() // Hide success
          .siblings(".w-form-fail")
          .show(); // show failure

        $("next-btn").attr("disabled", false);
        // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
        return;
      } else {
        // If form redirect setting set, then use this and prevent any other actions
        $form
          .hide() // optional hiding of form
          .siblings(".w-form-done")
          .show() // Show success
          .siblings(".w-form-fail")
          .hide(); // Hide failure

        redirectTo(XANO_USER_DASHBOARD_REDIRECT);
      }
    } else {
      redirectTo(`${XANO_USER_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  });
});
